<template lang="html">
    <el-table
        :data="tableData5"
        :row-class-name="setClassName"
        style="width: 100%">
        <el-table-column type="expand">
            <template slot-scope="props" >
                <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label="">
                        <span>{{ props.row.name }}</span>
                    </el-form-item>
                    <el-form-item label="">
                        <span>{{ props.row.shop }}</span>
                    </el-form-item>
                    <el-form-item label=" ID">
                        <span>{{ props.row.id }}</span>
                    </el-form-item>
                    <el-form-item label=" ID">
                        <span>{{ props.row.shopId }}</span>
                    </el-form-item>
                    <el-form-item label="">
                        <span>{{ props.row.category }}</span>
                    </el-form-item>
                    <el-form-item label="">
                        <span>{{ props.row.address }}</span>
                    </el-form-item>
                    <el-form-item label="">
                        <span>{{ props.row.desc }}</span>
                    </el-form-item>
                </el-form>
            </template>
        </el-table-column>
        <el-table-column
            label=" ID"
            prop="id">
        </el-table-column>
        <el-table-column
            label=""
            prop="name">
        </el-table-column>
        <el-table-column
            label=""
            prop="desc">
        </el-table-column>
    </el-table>
</template>

<script>
export default {
    data() {
        return {
            tableData5: [{
                id: '12987122',
                name: '',
                category: '',
                desc: '',
                address: '',
                shop: '',
                shopId: '10333',
                canExpand:true
            }, {
                id: '12987123',
                name: '',
                category: '',
                desc: '',
                address: '',
                shop: '',
                shopId: '10333',
                canExpand:true
                
            }, {
                id: '12987125',
                name: '',
                category: '',
                desc: '',
                address: '',
                shop: '',
                shopId: '10333',
                canExpand:false
            }, {
                id: '12987126',
                name: '',
                category: '',
                desc: '',
                address: '',
                shop: '',
                shopId: '10333',
                canExpand:false,
                expand:true
            }],
            // rowkey
            getRowKeys(row) {
                return row.id;
            },
            // rowkey
            expands: []
        }
    },
    methods:{
        setClassName({row, index}){
        // class
        return row.expand ? 'expand' : '';
    },
    },
    mounted() {
        // 
        this.expands.push(this.tableData5[1].id);
    }
}
</script>

<style lang="css">
.demo-table-expand {
   font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
.expand .el-table__expand-column .cell {
    display: none;
}
</style>


